<template>
  <div v-on:click="handleClick" v-on:mouseout="handleMouseOut" class="clipboard tooltip">
    <svg  
      class="clipboard-icon"
      data-tooltip-text="Click To Copy"
      data-tooltip-text-copied="✔ Copied"
      version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" xmlns:xlink="http://www.w3.org/1999/xlink" enable-background="new 0 0 512 512"><g><g><path d="m425.5,85.5h-61.9c-1.8-13.9-3.1-24.2-3.1-24.2-1.3-10.2-10-17.9-20.4-17.9h-63.5v-12c0-11.3-9.2-20.4-20.5-20.4-11.3,0-20.5,9.1-20.5,20.4v12h-63.6c-10.4,0-19.1,7.7-20.4,17.9l-3,24.2h-62c-11.3,0-20.5,9.1-20.5,20.4v374.6c0,11.3 9.2,20.4 20.5,20.4h338.9c11.3,0 20.5-9.1 20.5-20.4v-374.6c0.1-11.2-9.1-20.4-20.5-20.4zm-235.4-1.2h131.9l6.7,53.2h-145.3l6.7-53.2zm214.9,375.9h-297.8v-333.8h36.3l-3.6,29c-0.7,5.8 0,22.9 20.4,22.9h191.7c0.1,0 0.1,0 0.2,0 11.4,0 20.5-9.1 20.5-20.4 0-0.7-1.7-14.5-3.9-31.5h36.2v333.8z"/></g></g>
    </svg>
    <span class="tooltiptext">Copy to clipboard</span>
  </div>
</template>

<script>
export default {
  name: "Clipboard",
  methods: {
    // use query selectors to handle potential case of having multiple tooltips in future
    handleClick() {
      const tooltips = document.querySelectorAll('.tooltiptext');
      tooltips.forEach(tooltip => {tooltip.innerText = '✔ Copied'})
      this.$emit('copy');
    },
    handleMouseOut() {
      const tooltips = document.querySelectorAll('.tooltiptext');
      tooltips.forEach(tooltip => {tooltip.innerText = 'Copy to clipboard'})
    }
  }
};
</script>

<style>
.clipboard {
  position: relative
}

.clipboard-icon {
  pointer-events: none;
  height: 1rem;
  padding: 3px;
  border-radius: 5px;
  flex-shrink: 0;
}

.clipboard:hover .clipboard-icon {
  fill: var(--xi-orange);
  cursor: pointer;
}


.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 140px;
  background-color: var(--xi-blue);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1000;
  bottom: 120%;
  left: 50%;
  margin-left: -75px;
  opacity: 0;
  transition: opacity 0.6s;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: var(--xi-blue) transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

</style>
